export default {
	data() {
		return {
			nick_name: '',
			avatar_url: '',
			arts: [],
			user: {}
		};
	},
	onLoad() {
		//检测用户是否已经登录
		let loginRes = this.checkLogin('../my/my', 2);
		if (!loginRes) {
			return false;
		}
		this.nick_name = loginRes[2];
		this.avatar_url = loginRes[3];

	},
	methods: {}
};
